/* Hide placeholders in form elements when they are being focused
------------------------------------------------------------------------------------- */
@media all {
    ::placeholder {
        opacity: 1;
        transition: opacity 0.5s;
    }

    *:focus::placeholder {
        opacity: 0;
    }
}

/* Defaults
------------------------------------------------------------------------------------- */
:root {
    --white: rgb(255 255 255);
    --background-body-default: url("https://web-cdn.eveonline.com/aws/eveonline/sso/background.jpg") no-repeat center center fixed;
    --background-color-default: rgb(39 43 48);
    --font-color-default: rgb(200 200 200);
    --background-color-light-transparency: rgba(58 63 68 / 50%);
    --background-color-navigation-default-hover: rgb(62 68 76);
    --font-family-card-title: georgia, times, serif;
    --font-family-code: "Fira Code", monospace;
    --font-family-default: roboto, "Helvetica Neue", sans-serif;
    --backdrop-filter-default: saturate(1.5) blur(1.5em);
    --bs-font-monospace: var(--font-family-code);
    --bs-font-sans-serif: var(--font-family-default);
}

/* Code font ligatures */
@supports (font-variation-settings: normal) {
    :root {
        --font-family-code: "Fira Code VF", monospace;
    }
}
